        </vc-layer-imagery>
        <vc-provider-terrain-tianditu ref="terrain" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-terrain-tianditu>
        <vc-navigation></vc-navigation>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
