<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>string</td>
<td><code v-pre="">'https://{s}.tianditu.gov.cn/'</code></td>
<td><code v-pre="">required</code> 指定服务地址。</td>
</tr>
<tr>
<td>subdomains</td>
<td>Array</td>
<td><code v-pre="">['t0', 't1', 't2', 't3', 't4', 't5', 't6', 't7']</code></td>
<td>指定轮询子域名。</td>
</tr>
<tr>
<td>pluginPath</td>
<td>string</td>
<td><code v-pre="">'https://api.tianditu.gov.cn/cdn/plugins/cesium/cesiumTdt.js'</code></td>
<td><code v-pre="">optional</code> 指定天地图地形插件库地址。</td>
</tr>
<tr>
<td>dataType</td>
<td>string</td>
<td><code v-pre="">int</code></td>
<td><code v-pre="">optional</code> 指定数据类型。</td>
</tr>
<tr>
<td>tileType</td>
<td>string</td>
<td><code v-pre="">heightmap</code></td>
<td><code v-pre="">optional</code> 指定瓦片类型。</td>
</tr>
<tr>
<td>token</td>
<td>string</td>
<td></td>
<td><code v-pre="">optional</code> 指定天地图服务秘钥。</td>
</tr>
</tbody>
</table>
